@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;600;700&family=Open+Sans:wght@300;400;600&display=swap);
:root {
  --black: rgb(47, 48, 51);
  --white: #f8f8f8;
  --gold: rgb(200,165,106);
}
  
* {
  box-sizing: border-box;
  margin: 0;
  text-decoration: none;
}


::-webkit-scrollbar {
  width: 0.5rem;
  background: rgb(47, 48, 51);
  background: var(--black);
}

::-webkit-scrollbar-thumb {
  background: rgb(200,165,106);
  background: var(--gold);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #f8f8f8;
  background: var(--white);
  
}

body {
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(47, 48, 51);
  background-color: var(--black);
}

.App {
  display: -webkit-flex;
