.card-wrapper {    
    width: 19rem;
    height: 23rem;
    margin: 1.5rem 1rem;
    color: var(--white);
    position: relative;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.233) 1px 3px 5px 1px, rgba(0, 0, 0, 0.233) -1px 3px 5px 1px;
    background-color: rgba(0, 0, 0, 0.151);
}

.img-wrapper{
    width: 100%;
    height: auto;
    
}

.img-wrapper img {
    width: 100%;
    border-radius: 5px 5px 0 0 ;
}

.description-wrapper{
    display: -webkit-flex;
    display: flex;
    position: absolute;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 0;
    width: 100%;
    overflow: hidden;
    transition: .3s ease;
    background-color: rgba(47, 48, 51, 0.877);
    border-radius: 5px 5px 0 0;
    text-align: center;
    -webkit-transform: translate(0%, -100%);
    transform: translate(0%, -100%);
}

.card-wrapper:hover .description-wrapper {
    height: 84%;
}

.card-wrapper:hover .title {
    color: var(--gold);
}

.title {
    padding-top: 0.8rem;
    font-size: 1.2rem;
    color: rgb(235, 235, 235);
}

.description {
    font-size: 1.1rem;
    padding: 0 1rem 3rem;
    
}

.project-links {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
}

.github {
    width: 2rem;
    height: auto;
}

.card-anchor {
    text-decoration: none;
}

.card-anchor:link,    
.card-anchor:visited {
    color: black;
}

button {
    padding: 0.4rem;
