nav {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 5rem;
    width: 100%;;
    color: var(--gold);
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: rgba(58, 58, 58, 0.815);
    background-color: rgba(44, 44, 44, 0.815);
    /* border-bottom: 1px solid var(--gold); */
    z-index: 1;
}

.nav-wrapper {
    display: -webkit-flex;
    display: flex;
    width: 90%;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
}

.home, .projects, .contact {
    padding-top: 1rem;
    width: 7rem;
    font-size:0.9rem;
}

.nav-wrapper div:hover {
    color: var(--white);
}

.projects {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    cursor: pointer;
    
}

.open-dropdown {
    position: absolute;
    top: calc(100% + 1.4rem);
    background-color: rgba(58, 58, 58, 0.815);
    background-color:  rgba(40, 41, 48, 0.815);
    background-color: rgba(44, 44, 44, 0.815);
    border-left: 1px solid var(--gold);
    border-right: 1px solid var(--gold);
    border-bottom: 1px solid var(--gold);
    border-radius: 0 0 5px 5px;
    width: 7rem;
}

.open-dropdown p {
    padding-bottom: 1rem;
    color: var(--white);
}

.open-dropdown p:hover {
    color: var(--gold);

}

@media only screen and (min-width: 1024px) {
    .nav-wrapper {
        width: 30%;

    }
}
